import React from "react";
import Table from "react-bootstrap/Table";
import "./ErrorPopup.css";
import ReusableAlert from "../Alert/ReusableAlert";

const ErrorPopup = ({ errors }) => {
  console.log("Errors in ErrorPopup:", errors);

  // Optimized error grouping using Map
  const groupedErrors = new Map();

  errors.forEach((error) => {
    // Extract row number and column name using regex
    const rowMatch = error.match(/Row (\d+):/);
    const columnMatch = error.match(/"([^"]*)" is required/);

    const rowNum = rowMatch ? rowMatch[1] : "-"; // Get row number or default to "-"
    const columnName = columnMatch ? columnMatch[1] : "-"; // Get column name or default to "-"
    const errorMsg = rowMatch ? error.split(": ")[1] : error; // Extract error message

    // If the row doesn't exist in the Map, initialize it
    if (!groupedErrors.has(rowNum)) {
      groupedErrors.set(rowNum, { errorMessages: [], columnNames: [] });
    }

    // Get existing entry and update it
    const rowData = groupedErrors.get(rowNum);
    rowData.errorMessages.push(errorMsg);
    rowData.columnNames.push(columnName);
  });

  // Convert Map to a plain object for rendering
  const groupedErrorsObj = Object.fromEntries(groupedErrors);

  const errorMessage = (
    <>
      <strong>Note:</strong> Below reports show only uploaded spreadsheet error
      rows. To resolve below issues, do the required changes in the spreadsheet
      and re-import.
    </>
  );

  return (
    <>
      <ReusableAlert
        variant="danger"
        message={errorMessage}
        className="text-left-align text-sm-start mb-3"
      />
      <div className="table-responsive">
        <Table striped bordered hover>
          <thead className="table-light">
            <tr>
              <th>Row Number</th>
              <th>Error Description</th>
              <th>Column Name</th>
            </tr>
          </thead>
          <tbody>
            {Object.keys(groupedErrorsObj).length > 0 ? (
              Object.entries(groupedErrorsObj).map(
                ([rowNum, { errorMessages, columnNames }]) => (
                  <tr key={rowNum}>
                    <td>{rowNum}</td>
                    <td>{errorMessages.join(", ")}</td>
                    <td>{columnNames.join(", ")}</td>
                  </tr>
                )
              )
            ) : (
              <tr>
                <td colSpan={3} className="text-center">
                  No Errors Found
                </td>
              </tr>
            )}
          </tbody>
        </Table>
      </div>
    </>
  );
};

export default ErrorPopup;
